.form__col .form__row:not(:last-child) { margin-bottom: 10px; }
@media only screen and (max-width: 767px) { .form__col .form__row:not(:last-child) { margin-bottom: 15px; } }
.form__col .form__label { margin-left: 5px; text-transform: uppercase; }
.form__row:not(:last-child) { margin-bottom: 20px; }
.form__row.clr { *zoom: 1; }
.form__row.clr:after { content: " "; display: table; clear: both; }
.form__row_mt { margin-top: 30px; }
.form__label { display: block; margin-bottom: 5px; font-size: 16px; letter-spacing: 0.01em; color: #4f4f4f; margin-left: 15px; font-family: MyriadPro-Semi, sans-serif; }
.form__input, .form__textarea { width: 100%; padding: 5px 24px; border: 1px solid #cbcbcb; border-radius: 6px; box-sizing: border-box; }
.form__input { height: 44px; font-size: 24px; line-height: 44px; }
.form__input::placeholder { font-size: 24px; color: #898989; }
.form__textarea { min-height: 125px; resize: none; padding: 5px 10px; font-size: 18px; }
.form__select { width: 100%; height: 44px; line-height: 44px; border: 1px solid #cbcbcb; border-radius: 6px; }
.form__link { font-size: 16px; letter-spacing: 0.01em; text-decoration: none; font-family: MyriadPro-Semi, sans-serif; }
.form__link_red { color: #cc2b23; }
.form__link:hover { text-decoration: underline; }

.select2-search { display: none; }

.select2-container--default .select2-selection--single .select2-selection__clear { display: none; }

.select2-container .select2-selection--single { height: 44px; border: 1px solid #cbcbcb; border-radius: 6px; }

.select2-container--default .select2-selection--single .select2-selection__rendered { font-size: 18px; line-height: 44px; }

.select2-container--default .select2-selection--single .select2-selection__arrow { top: 7px; right: 11px; }

.select2-container--default .select2-selection--single .select2-selection__arrow b { border-width: 9px 5px 0 5px; border-color: #cc2b23 transparent transparent transparent; }

.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b { border-width: 0 5px 9px 5px; border-color: transparent transparent #cc2b23 transparent; }

.select2-dropdown { border: 1px solid #cbcbcb; border-radius: 6px; }

.select2-container--default .select2-results__option--highlighted[aria-selected] { background-color: #CC2B23; }

.select2-results__option:last-child { border-radius: 0 0 6px 6px; }


